// pages / dashboard / account / view

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class AccountView extends React.Component {
    componentDidMount() {
        $(".select-acc-type").select2({
            placeholder: "Select Account Type",
            allowClear: true,
            width: '100%'
        });

        $(".select-property-type").select2({
            placeholder: "Select Property Type",
            allowClear: true,
            width: '100%'
        });
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Account - view" meta={[
                        {
                            name: 'description',
                            content: 'Account - view'
                        }, {
                            name: 'keywords',
                            content: 'Account'
                        }
                    ]}/>
                    {/* End of Helmet */}

                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-10">
                            <h2>View Account</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>View Account</strong>
                                </li>
                            </ol>
                        </div>
                        <div className="col-lg-2">

                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Blueimp Gallery Lightbox */}
                    <div id="blueimp-gallery" className="blueimp-gallery blueimp-gallery-controls">
                        <div className="slides"></div>
                        <h3 className="title"></h3>
                        <a className="prev">‹</a>
                        <a className="next">›</a>
                        <a className="close">×</a>
                        <a className="play-pause"></a>
                        <ol className="indicator"></ol>
                    </div>
                    {/* End of Blueimp Gallery Lightbox */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInRight">
                        <div className="row">
                            <div className="col-md-6">
                                <div className="profile-image">
                                    <img src="https://i.pinimg.com/originals/81/60/19/816019c2d4e30e67f9f2a5b00fb3409a.jpg" className="img-circle circle-border m-b-md" alt="profile" />
                                </div>
                                <div className="profile-info">
                                    <div className="">
                                        <div>
                                            <h2 className="no-margins">
                                                Xavier Dellas
                                            </h2>
                                            <h4>Security Guard</h4>
                                            <small>
                                                Aged 25. Very hardworking young men, energertic and treat all people with equality, justice and empathy.
                                            </small>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="clearfix"></div>

                            <div className="col-md-3">
                                <div className="ibox">
                                    <div className="ibox-content">
                                        <h3>About Xavier Dellas</h3>
                                        <div className="space-10"></div>

                                        <p className="small">
                                            <i className="fa fa-phone"></i> 018-3029485
                                            <br/><br/>
                                            <i className="fa fa-envelope"></i> xavierdellas@gmail.com
                                        </p>
                                        <div className="space-25"></div>

                                        <p className="small">
                                            His working swift is from 6am till 6pm, everyday. People in the residence well verse with him due to his friendly manner towards anybody from young to old. He enjoy working in the neighbourhood.
                                            <br/>
                                            <br/> Aged 25. A hardworking young men, energertic and treat all people with equality, justice and empathy.
                                        </p>

                                    </div>
                                </div>
                            </div>

                            <div className="col-md-6">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-content">
                                        <h3>Photos</h3>

                                        <div className="lightBoxGallery">
                                            <a href="https://i.pinimg.com/originals/00/e4/dd/00e4dd00374818b0594dbc23c90f03e3.jpg" title="Demo Image" data-gallery="">
                                                <img className="square-200" src="https://i.pinimg.com/originals/00/e4/dd/00e4dd00374818b0594dbc23c90f03e3.jpg" />
                                            </a>
                                            <a href="https://i.pinimg.com/originals/2c/3a/50/2c3a505a08cd4170e5a1eb1dad8f050c.jpg" title="Demo Image" data-gallery="">
                                                <img className="square-200" src="https://i.pinimg.com/originals/2c/3a/50/2c3a505a08cd4170e5a1eb1dad8f050c.jpg" />
                                            </a>
                                            <a href="https://i.pinimg.com/originals/53/32/d1/5332d1b06f8f1e196b1be83582863afc.jpg" title="Demo Image" data-gallery="">
                                                <img className="square-200" src="https://i.pinimg.com/originals/53/32/d1/5332d1b06f8f1e196b1be83582863afc.jpg" />
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>


                        </div>
                    </div>
                    {/* End of Content */}


                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(AccountView);



